/* Posts columns */
th.manage-column.column-ymfseo {
  width: 40px;
  text-align: center;
}

td.ymfseo.column-ymfseo {
  vertical-align: middle;
}

.column-ymfseo__dot {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.column-ymfseo__dot span {
  background-color: #f0f0f0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  display: block;
}
.column-ymfseo__dot span.good {
  background-color: #46B450;
}
.column-ymfseo__dot span.bad {
  background-color: #dc3232;
}

/* Meta box */
#ymfseo_fields {
  /* Common */
  /* For Gutenberg */
}
#ymfseo_fields > .inside {
  margin-top: 0;
  padding: 16px;
}
#ymfseo_fields > .inside .ymfseo-box {
  /* Field */
  /* Checkbox */
}
#ymfseo_fields > .inside .ymfseo-box__page {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
#ymfseo_fields > .inside .ymfseo-box details:not([open]) + details {
  margin-top: -16px;
}
#ymfseo_fields > .inside .ymfseo-box details summary {
  margin-bottom: 10px;
  cursor: pointer;
  text-transform: uppercase;
}
#ymfseo_fields > .inside .ymfseo-box__field-box {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
#ymfseo_fields > .inside .ymfseo-box__field-box label {
  font-size: 11px;
  font-weight: 500;
  line-height: 1.4;
  text-transform: uppercase;
  display: inline-block;
}
#ymfseo_fields > .inside .ymfseo-box__field-box textarea {
  height: auto;
}
#ymfseo_fields > .inside .ymfseo-box__field-box p {
  margin: 2px 0 0 0;
  font-size: 12px;
  color: rgb(117, 117, 117);
}
#ymfseo_fields > .inside .ymfseo-box__check-box {
  display: flex;
  align-items: center;
  gap: 2px;
}
#ymfseo_fields:has(.ymfseo-box_gutenberg) > .postbox-header h2 {
  padding: 0 16px !important;
  font-size: 13px;
}
#ymfseo_fields:has(.ymfseo-box_gutenberg) > .inside {
  padding: 5px 16px 16px 16px !important;
}
#ymfseo_fields:has(.ymfseo-box_gutenberg) .ymfseo-box__check-box {
  gap: 10px;
}

.ymfseo-tags-table {
  width: 100%;
  margin: -5px 0;
  border-spacing: 0 4px;
  table-layout: fixed;
}
.ymfseo-tags-table tr td:first-child {
  display: flex;
}
.ymfseo-tags-table tr td:first-child span {
  background-color: #f0f0f0;
  margin-right: 6px;
  padding: 1px 4px 3px 4px;
  border-radius: 4px;
  display: block;
}
.ymfseo-tags-table tr td:last-child {
  word-wrap: break-word;
}

.postbox-container #side-sortables:has(.ymfseo-box) #ymfseo_fields > .inside {
  padding: 12px;
}